<template>
  <section class="page login" v-cloak>

    <form action="#" class="form" @submit.prevent="login( formLog )">
      <v-input v-for="el in arrInput" :key="el" v-model="formLog[ el.where ]" :settings="el.settings"></v-input>
      <button class="form__btn" type="submit" :disabled="!disabledBtn">Войти</button>
    </form>

  </section>
</template>

<script>
import vInput from '@/scripts/components/vInput.vue'
import { mapActions } from 'vuex'

export default {
  name: 'LoginView',

  data: () => ({
    arrInput: [
      { 
        where: 'email',
        settings: { 
          title: 'Введите почту', dbClass: null, placeholder: 'asik@gmail.com', min: 0, max: 64, type: 'email', id: 'logEmail'
        }
      },

      { 
        where: 'password',
        settings: { 
          title: 'Введите пароль', dbClass: null, placeholder: 'C8YF9W', min: 6, max: 12, type: 'password', id: 'logPass'
        }
      },
    ],

    formLog: {
      password: '',
      email: '',
    },
  }),

  watch: {

  },
  
  methods: {
    ...mapActions([
      'login'
    ])
  },

  computed: {
    disabledBtn() {
      return this.formLog.email && this.formLog.password
    }
  },

  components: {
    'v-input': vInput
  },

  created() {
  
  }
}
</script>